import React from 'react'

const AboutMe = () => {
  return (
    <section className="container mx-auto flex justify-center items-center h-screen p-10" >
      <article className="bg-white bg-opacity-80 blur rounded-lg p-6 w-full">

        <ul className="flex flex-wrap gap-y-4">
          <li className="w-full flex flex-wrap lg:px-20">
            <p className="w-1/3 text-right pr-4">Name</p>
            <p className="w-2/3">Lim Xin Ze</p>
          </li>
          <li className="w-full flex flex-wrap lg:px-20">
            <p className="w-1/3 text-right pr-4">Age</p>
            <p className="w-2/3">20 ( 2001 )</p>
          </li>
          <li className="w-full flex flex-wrap lg:px-20">
            <p className="w-1/3 text-right pr-4">Country</p>
            <p className="w-2/3">Malaysia</p>
          </li>
          <li className="w-full flex flex-wrap lg:px-20">
            <p className="w-1/3 text-right pr-4">Hobby</p>
            <p className="w-2/3">FPS Game , RPG Game , Sing , Cook , code , Draw ...</p>
          </li>
        </ul>
        
      </article>
    </section>
  )
}

export default AboutMe
