import React from 'react'
import { Link } from 'react-router-dom'

// importing user Picture
import User from "../../image/user_face.png"


function UserImage(porps) {
    return (<article className={`md:w-3/5 md:py-12 md:px-20 w-full `+porps.className}>
        <img src={User} alt="Owner Website" className="md:w-auto w-1/3" />
    </article>);
}


const Portfolio = () => {
    return (
        <section className='container mx-auto h-screen'>
            <article className=" p-4 mx-auto md:h-full flex flex-wrap md:justify-center md:items-center md:static md:top-0 md:-translate-y-0 md:transform-none absolute top-1/2 transform -translate-y-1/2">
                <article className="md:relative md:w-2/5 w-full px-4">
                    <div className="md:absolute md:top-1/2 md:transform md:-translate-y-1/2 z-10">
                        <h1 className="text-yellow-500 text-4xl bold">Hi , I'm Limzhe</h1>
                        <h2 className="text-yellow-500 text-opacity-80 text-2xl"> a website focused Developer / Designer</h2>
                        <p className="text-white"> - I 'm ready to help you build your website become fashion</p>
                        <Link to="/social" className="text-blue-500 hover:text-blue-600 text-xl mt-4 block cursor-pointer group">Contact Me <span className="relative transition delay-100 -right-0 group-hover:-right-1"><i className="fas fa-caret-right"></i></span></Link>
                    </div>
                </article>
                <UserImage className="flex items-center justify-center mt-5 md:mt-0"></UserImage>
            </article>
        </section>
    )
}

export default Portfolio
