import React from 'react'

const socialItemBoxStyle = " bg-yellow-500 rounded-full w-20 h-20 relative cursor-pointer"

const Social = () => {
    return (
        <section className="container mx-auto p-4 ">
            <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-white">
                <article className="flex flex-wrap mx-auto gap-x-6 gap-y-4 justify-center items-center">
                    <a href="https://www.facebook.com" className={socialItemBoxStyle} target="_blank" rel="noreferrer">
                        <i class="fab fa-facebook-f text-blue-600 text-3xl absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2"></i>
                    </a>
                    <a href="https://www.twitter.com" className={socialItemBoxStyle} target="_blank" rel="noreferrer">
                        <i class="fab fa-twitter text-blue-600 text-3xl absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2"></i>
                        
                    </a>
                    <a href="https://www.youtube.com" className={socialItemBoxStyle} target="_blank" rel="noreferrer">
                        <i class="fab fa-youtube text-red-600 text-3xl absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2"></i>
                    </a>
                    <a href="mailto:limxinze@gmail.com" className={socialItemBoxStyle} >
                        <i class="fas fa-envelope text-blue-600 text-3xl absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2"></i>
                    </a>
                </article>
            </div>
        </section>
    )
}

export default Social
