import { Link } from "react-router-dom"
import { useState ,useEffect} from "react";

const navItemStyle = "w-full hover:text-yellow-500 transition-colors delay-75" ;

const Navbar = () => {
    // nav bar btn on off in mobile
    const [navBtnMenu , setNavBtnMenu ] = useState(false)
    const windowWidth = window.innerWidth
    useEffect(() => {
        windowWidth <=786 && setNavBtnMenu(true)
    }, [windowWidth])
    return (
        <>
        <nav className={`
        
        absolute 
        ${navBtnMenu && "hidden" }
        flex
        justify-center items-center

        md:transform md:right-5 md:top-1/2 md:-translate-y-1/2 
        md:h-auto md:w-auto md:bg-transparent md:blur-none 

        h-screen w-screen bg-gray-300 blur-xl bg-opacity-90 z-30 
        
        `}>
            <ul className="
            md:text-white md:text-right md:w-auto

            text-black font-bold text-center w-screen 
            ">
                <Link to="/" className={navItemStyle}
                >
                    <li className="md:py-1 py-4">Home</li>
                </Link>
                <Link to="/aboutme" className={navItemStyle}
                >
                    <li className="md:py-1 py-4">About Me</li>
                </Link>
                <Link to="/myskill" className={navItemStyle}
                >
                    <li className="md:py-1 py-4">My Skills</li>
                </Link>
                <Link to="/social" className={navItemStyle}
                >
                    <li className="md:py-1 py-4">Social</li>
                </Link>
            </ul>
        </nav>
        <nav 
        className="w-16 h-16 bg-yellow-500 shadow-lg rounded-full absolute top-0 right-0 m-4 flex justify-center items-center z-40 md:hidden"
        onClick={e=>{
            setNavBtnMenu(!navBtnMenu)
        }}
        >
        {
            navBtnMenu ? 
            (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-7 w-7 text-white" viewBox="0 0 20 20" fill="currentColor">
                    <path fill-rule="evenodd" d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clip-rule="evenodd" />
                </svg>
            ):(
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
            )
        }
        </nav>
        </>
    )
}

export default Navbar
